import React from 'react';
import styles from './Article.module.css';
import PDF from '../PDF';

const page = () => {
  return (
    <div className={styles.article}>
        <h3 className={styles.subtitle}>Présentation du jeu d'Awélé</h3>
        <div className= {styles.paragraph_100}>
            <p>Le jeu d’Awélé est un jeu de stratégie traditionnel originaire d’Afrique, appartenant à la famille des jeux de semailles, aussi connus sous le nom de Mancala. Il se joue généralement à deux joueurs et se déroule sur un plateau composé de deux rangées de six trous, appelés "cases". Chaque joueur contrôle une rangée et doit capturer un maximum de graines tout en empêchant son adversaire de faire de même.</p>

            <h4>Règles de base :</h4>
            <ul className={styles.list}>
                <li>Au début du jeu, chaque case contient un certain nombre de graines (souvent 4 par case).</li>
                <li>Les joueurs jouent à tour de rôle. À chaque tour, un joueur prend toutes les graines d'une de ses cases et les sème une à une dans les cases suivantes, dans le sens antihoraire.</li>
                <li>Si la dernière graine semée termine dans une case de l’adversaire contenant désormais 2 ou 3 graines, le joueur capture ces graines. Il peut également capturer d'autres graines si les cases précédentes respectent cette même condition.</li>
                <li>Le jeu se termine lorsque l’un des joueurs n’a plus de graines dans ses cases, ou lorsque l'un des joueurs ne peut plus effectuer de mouvements légaux.</li>
            </ul>

            <h4>Objectif :</h4>
            <p>Le but du jeu est de capturer plus de graines que son adversaire. Une partie peut être à la fois rapide et tactique, nécessitant une bonne anticipation des mouvements adverses et une gestion stratégique des graines.</p>

            <p>Le jeu d’Awélé est à la fois simple à comprendre, mais offre une grande profondeur stratégique, ce qui en fait un terrain d’entraînement idéal pour concevoir et tester des intelligences artificielles.</p>

        </div>
        <h2 className={styles.subtitle} >Que le meilleur bot gagne !</h2>
        <div className= {styles.paragraph_50} >
        <p>Nous avons dû réalisé un projet autour du jeu traditionnel africain Awélé, où chaque groupe de deux ou seul devait concevoir une intelligence artificielle capable de jouer de manière autonome et performante contre des adversaires virtuels.</p>

        <p>Les performances des projets ont été évaluées à travers plusieurs parties simulées donnant un nombre de points entre 0 et 3 à chaque affrontement, en tenant compte du taux de victoire, du temps de calcul des coups, et de la capacité à anticiper les stratégies adverses.</p>

        <p>Mon projet, nommé TYSONBOT, a obtenu des résultats remarquables lors de ces évaluations en arrivant 2eme position de la compétition. L'algorithme MinMax avec optimisation Alpha-Bêta que j'ai implémenté a montré une grande efficacité dans la prise de décision, dépassant les performances des autres projets en termes de profondeur de recherche et de rapidité d'exécution.</p>

        <p>Vous pouvez voir un compte rendu plus détaillé de ce projet ci-dessous.</p>

        </div>
        <div className={styles.paragraph_50} >
         <img className={styles.paragraph_50} src={process.env.PUBLIC_URL + '/data/images/AweleConcour.png'} alt="AweleConcour.png"></img>
        </div>
        
        <h2 className={styles.subtitle} >Compte rendu</h2>
        <PDF filePath={`${process.env.PUBLIC_URL}/data/pdf/TYSONBOT_Alexandre_Untereiner.pdf`} />
    </div>
  );
}

export default page;